html {
	overflow-x: hidden;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	--scrolltrack: white;
	--border-color: #f0f0f0;
	overflow-x: hidden;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

#disclaimer {
	width: 100%;
	background-color: rgb(250, 222, 60, 0.9);
	font-weight: 600;
	font-size: 1.5vmin;
}

#disclaimer-detail {
	margin-left: 10px;
}

#disclaimer-links {
	display: flex;
}

#disclaimer-authoritative {
	display: flex;
}

.disclaimer-link-head {
	margin-left: 10px;
}

.disclaimer-links-address {
	margin-left: 5px;
	color: inherit;
	text-decoration: underline;
}

#options {
	font-size: 1.1vmin;
	width: 100%;
	height: 100px;
	display: inline-flex;
	position: sticky;
	top: 0;
	justify-content: space-around;
	flex-wrap: wrap;
	align-items: center;

	z-index: 10;
	background-color: var(--scrolltrack);
}

#options-theme {
	display: inline-flex;
	flex-direction: column;
}

.options-inputs {
	width: 200px;
}

#summary {
	display: flex;
	justify-content: center;
}

#summary-card {
	width: 75%;
	display: flex;
	justify-content: center;
}

