} */

.ant-statistic-title {
	/* font-size: 1.7vmin; */
}

#tables {
	width: 85%;
	margin-left: auto;
	margin-right: auto;
}

.tables-header {
	font-size: 20px;
	font-weight: bold;
	margin-bottom: 10px;
	border: 1px solid var(--border-color);
}

.tables-individual {
	height: auto;
	margin-top: 20px;
	background-color: var(--scrolltrack);
	border: 1px solid var(--border-color);
	margin-bottom: 20px;
	margin-left: auto;
	margin-right: auto;
	padding: 10px;
	border-radius: 3px;
}

.ReactVirtualized__Grid::-webkit-scrollbar,
.ReactVirtualized__Table__Grid::-webkit-scrollbar,
.ReactVirtualized__Table__row::-webkit-scrollbar {
	background-color: var(--scrolltrack);
}

.ReactVirtualized__Grid::-webkit-scrollbar-thumb,
.ReactVirtualized__Table__Grid::-webkit-scrollbar-thumb,
.ReactVirtualized__Table__row::-webkit-scrollbar-thumb {
	background-color: grey;
}

.ReactVirtualized__Table__row,
.ReactVirtualized__Table__row {
	width: 100% !important;
	justify-content: space-evenly;
}
.ReactVirtualized__Grid__innerScrollContainer,
.ReactVirtualized__Grid,
.ReactVirtualized__Table__Grid,
.ReactVirtualized__Table__headerRow {
	width: inherit !important;
	max-width: inherit !important;
}

.ReactVirtualized__Table__headerRow {
	padding-right: 25px !important;
}

.ReactVirtualized__Table,
.ReactVirtualized__Grid {
	max-height: 500px !important;
}

.ReactVirtualized__Table__Grid {
	max-height: 440px !important;
	min-height: 30px !important;
}

.ReactVirtualized__Table__headerColumn {
	display: flex;
	align-items: center;
	height: 40px;
	font-size: 1.7vmin;
}

.ReactVirtualized__Table__headerRow {
	border: 1px solid var(--border-color);
	margin-bottom: 10px;
	border-radius: 3px;
	justify-content: space-evenly;
}
.ReactVirtualized__Table__headerTruncatedText {
	overflow: auto !important;
	overflow-wrap: break-word !important;
	word-wrap: break-word !important;
	white-space: pre-line !important;
}

.ReactVirtualized__Table__rowColumn {
	height: inherit;
	font-size: 1.7vmin;
}
